<template>
  <div>
    <form  @submit.prevent="submit">
      <div class="row justify-content-center">
        <div class="offset-1 col-9">
          <div class="shadow rounded-lg p-3">
            <button type="button" @click="$router.push(`/app/covid/admin/kategori/addMarker?id=${vdata.id}&nama=${vdata.kategori}`)" v-if="vdata.id" class="btn btn-sm btn-dark  float-right"><span class="typcn typcn-pin"></span> Add Marker</button>
            <!-- <button type="button" @click="$router.push(`/app/covid/admin/kategori/addMarkerCovid?id=${vdata.id}&nama=${vdata.kategori}`)" v-if="vdata.id" class="btn btn-sm btn-dark  float-right"><span class="typcn typcn-pin"></span> Add Marker Covid</button> -->
            <button type="button" @click="$router.push(`/app/covid/admin/kategori/geojson?id=${vdata.id}&nama=${vdata.kategori}`)" v-if="vdata.id" class="btn btn-sm btn-dark  "><span class="typcn typcn-map"></span> Add GeoJson</button>
            <hr>
          <!-- INPUT -->
          <div class="sm-form">
            <input
              type="text"
              id="id"
              name="id"
              class="form-control form-control-sm"
              placeholder="id"
              v-model="vdata.id"
              disabled
            />
          </div>
        <div class="sm-form ">
            <label for="kategori">Kategori Peta/Map : </label>
            <input required type="text" id="kategori" name="kategori" class="form-control form-control-sm" placeholder="kategori" v-model="vdata.kategori" >
        </div>
         <!-- <div class="sm-form ">
             <label for="lat">Latitude : </label>
             <input type="text" id="lat" name="lat" class="form-control form-control-sm" placeholder="lat" v-model="vdata.latitude" >
         </div>
         <div class="sm-form ">
             <label for="long">Longitude : </label>
             <input type="text" id="long" name="long" class="form-control form-control-sm" placeholder="long" v-model="vdata.longitude" >
         </div> -->
          <!-- END INPUT -->
          <!-- BUTTON PART -->
          <div class="mt-3 p-3">
            <button
              type="submit"
              class="btn btn-sm"
              :class="
                btn == 'tambah'
                  ? 'btn-primary'
                  : btn == 'update'
                  ? 'btn-success'
                  : 'btn-danger'
              "
            >
              <span class="typcn typcn-info"></span>  Proses {{ btn }}
            </button>
            <div class="float-right">
              <button
                type="button"
                v-show="btn!='tambah'"
                @click="
                  btn = 'tambah';
                  vdata = {};
                "
                class="btn btn-sm btn-dark"
              >
                <span class="typcn typcn-edit"></span> Tambah
              </button>
              <button
                type="button"
                @click="btn = 'delete'"
                v-show="btn!='tambah'"
                class="ml-3 btn btn-sm btn-danger"
              >
                <span class="typcn typcn-delete"></span> Delete
              </button>
            </div>
          </div>
          <!-- END BUTTON PART -->
        </div>
      </div>
      </div>
    </form>
    <!-- TABLE PART -->
    <div class="row justify-content-center">
      <div class="col-md-9 sm:ml-5">
            <div v-if="datanya.length>0" class="p-3">
            <Btables :tables="datanya"  :cari="['']" :hide="['latitude','longitude']" @selected="ambil" ref="btable" />
            </div>
      </div>
    </div>
    <!-- END TABLE PART -->
  </div>
</template>

<script>
// TOTURIAL 
// ganti source autophp, ganti source component btable, ganti path table sdb
import autophp from "@/plugins/autophp_gis.js"; // ganti source nya
import Btables from "@/components/Btables.vue"; //component btable nya
let sdb = new autophp();
export default {
  components: {
    Btables,
  },
  data() {
    return {
      selected: false,
      vdata: {},
      btn: "tambah",
      datanya:[],
    };
  },
  methods: {
    upload(){
        let that=this;
        let file = document.querySelector("#filenya"); // berikan id pada input file
        sdb.collection('tbuser').upload(file).then(res=>{
           that.vdata.geojson=res
           that.$forceUpdate();
        })
    },
    test(){
      this.$refs.btable.getData()
    },
    getData(){
      sdb.collection("app_gis_kategori").doc().select(`select * from app_gis_kategori`).then(res => {
        this.datanya=res;
        this.$forceUpdate();
      });
      
    },
    ambil(data) {
      this.vdata = data;
      this.btn = "update";
      this.$forceUpdate();
    },
    submit() {
      let that=this
      if (confirm("Apakah yakin proses dilanjutkan ?")) {
        if (this.btn == "tambah") {
          //idnya letak di vdata
          delete this.vdata.id;
          sdb
            .collection("app_gis_kategori")
            .doc()
            .set(this.vdata)
            .then((res) => {
              that.getData();
            // that.$nuxt.$emit('getData')

            });
        } else if (this.btn == "update") {
          sdb
            .collection("app_gis_kategori")
            .doc()
            .set(this.vdata)
            .then((res) => {
              that.getData();
            // that.$nuxt.$emit('getData')
            });
        } else if (this.btn == "delete") {
          sdb
            .collection("app_gis_kategori")
            .doc(this.vdata.id)
            .delete()
            .then((res) => {
              that.getData();
            // that.$nuxt.$emit('getData')
            });
        }
        // this.$nuxt.$emit('getData','ok')
      }
    },
  },
  async mounted() {
    this.getData();
  },
layout:'app_gis',
};
</script>